body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  color: #eee;
  background: linear-gradient( 135deg, rgba(48, 140, 179, .6), rgba(48, 140, 179, .1)), linear-gradient( 180deg, transparent, rgba(0, 0, 0, .4)), linear-gradient( 60deg, rgba(32, 93, 119, .1), rgba(32, 93, 119, .1) 50%, rgba(32, 93, 119, 0) 50.1%), #000;
  background-attachment: fixed;
}

.card {
  max-width: 300px;
  margin: 3rem auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
